<template>
  <span v-if="isVisible()">
    <el-button
      id="btn-add-order"
      type="success"
      plain
      @click="dialogVisible = true"
      size="mini"
      icon="el-icon-plus"
    >{{ $t('warehouse.orderEntry.addOrder') }}</el-button>
    <el-dialog
      :close-on-click-modal="false"
      v-on:open="handleOpen"
      :title="$t('warehouse.orderEntry.addOrder')"
      :visible.sync="dialogVisible"
      width="85%"
      :before-close="handleClose"
    >
      <el-form
        :model="form"
        :rules="formRules"
        ref="form"
        v-circular-tab-navigation-dialog
        label-position="top"
        size="mini"
      >
        <el-col :span="24">
          <el-col :md="9" :sm="12" :xs="24" class="form-vertical-item">
            <el-form-item :label="$t('warehouse.orderEntry.consignee')" prop="consigneeAccountId">
              <autocomplete
                :model.sync="form.consigneeAccountId"
                :url="urlConsignee"
                :shipperAccountId="orderEntry.settings.shipperAccountId"
                labelField="name"
                labelFieldLastWithDash="number"
                valueField="id"
                id="form-consignee"
                ref="form-consignee"
              />
            </el-form-item>
          </el-col>
          <el-col :md="4" :sm="12" :xs="12" class="form-vertical-item">
            <el-form-item :label="$t('warehouse.orderEntry.unitOfMeasure')" prop="unitOfMeasure">
              <el-select v-model="form.unitOfMeasure" id="form-unit-of-measure">
                <el-option
                  v-for="item in unitOfMeasureOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :md="3" :sm="12" :xs="12" class="form-vertical-item">
            <el-form-item :label="$t('warehouse.orderEntry.measure')" prop="measure">
              <el-select v-model="form.measure" id="form-measure">
                <el-option
                  v-for="item in measureOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                  ref="measure"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :md="5" :sm="12" :xs="12" class="form-vertical-item">
            <el-form-item :label="$t('warehouse.orderEntry.shipDate')" prop="shipDate">
              <el-date-picker
                v-model="form.shipDate"
                @change="handleChangeDate"
                :default-value="orderEntry.actualFilters.shipDate"
                style="width: 100%"
                format="MM/dd/yyyy"
                :picker-options="orderEntry.shipDatePickerOptions"
                type="date"
                id="form-ship-date"
              ></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :md="3" :sm="12" :xs="12" class="form-vertical-item">
            <el-form-item :label="$t('warehouse.orderEntry.numberOfUnitsShort')" prop="totalUnits">
              <el-input-number
                v-model="form.totalUnits"
                v-numeric-validation
                :precision="0"
                :min="0"
                :max="999"
                class="inline-input width-100p"
                id="form-number-units"
                ref="unit"
              ></el-input-number>
            </el-form-item>
          </el-col>
        </el-col>
        <el-col :span="24">
          <el-col :md="2" :sm="12" :xs="12" class="form-vertical-item">
            <el-form-item :label="$t('warehouse.orderEntry.length')" prop="length">
              <el-input-number
                v-model="form.length"
                v-numeric-validation
                :controls="false"
                :precision="3"
                :min="0"
                :max="999.999"
                class="inline-input width-100p"
                id="form-length"
                ref="length"
              ></el-input-number>
            </el-form-item>
          </el-col>
          <el-col :md="2" :sm="12" :xs="12" class="form-vertical-item">
            <el-form-item :label="$t('warehouse.orderEntry.width')" prop="width">
              <el-input-number
                v-model="form.width"
                v-numeric-validation
                :controls="false"
                :precision="3"
                :min="0"
                :max="999.999"
                class="inline-input width-100p"
                id="form-width"
                ref="width"
              ></el-input-number>
            </el-form-item>
          </el-col>
          <el-col :md="2" :sm="12" :xs="12" class="form-vertical-item">
            <el-form-item :label="$t('warehouse.orderEntry.height')" prop="height">
              <el-input-number
                v-model="form.height"
                v-numeric-validation
                :controls="false"
                :precision="3"
                :min="0"
                :max="999.999"
                class="inline-input width-100p"
                id="form-height"
                ref="height"
              ></el-input-number>
            </el-form-item>
          </el-col>
          <el-col :md="3" :sm="12" :xs="12" class="form-vertical-item">
            <el-form-item :label="$t('warehouse.orderEntry.product')" prop="productCode">
              <autocomplete
                :model.sync="form.productCode"
                :modelAdditional.sync="form.productDescription"
                :strict="false"
                :url="urlProduct"
                labelField="description"
                labelfieldSelected="code"
                valueField="code"
                valueFieldAdditional="description"
                :maxlength="5"
                id="form-product"
                ref="form-product"
              />
            </el-form-item>
          </el-col>
          <el-col :md="6" :sm="24" :xs="24" class="form-vertical-item">
            <el-form-item
              :label="$t('warehouse.orderEntry.productDescription')"
              prop="productDescription"
            >
              <el-input
                v-model="form.productDescription"
                v-alphanumeric-validation
                maxlength="30"
                clearable
                class="inline-input"
                id="form-product-description"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :md="3" :sm="12" :xs="12" class="form-vertical-item">
            <el-form-item :label="$t('warehouse.orderEntry.poNumber')" prop="po">
              <el-input
                v-model="form.po"
                v-alphanumeric-validation
                maxlength="25"
                clearable
                class="inline-input"
                id="form-po-number"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :md="3" :sm="12" :xs="12" class="form-vertical-item">
            <el-col :md="3" :sm="12" :xs="12" class="form-vertical-item">
              <el-form-item :label="$t('warehouse.orderEntry.farmName')" prop="farmName">
                <transition name="slide">
                  <div class="shipper-list" v-if="show">
                    <div class="title">{{ $t('shipperComponent.shipperList') }}</div>
                    <div class="search-shippers-div">
                      <el-input
                        v-model="searchText"
                        v-alphanumeric-validation
                        maxlength="50"
                        clearable
                        @change="search"
                        class="inline-input"
                        size="mini"
                        suffix-icon="el-icon-search"
                        id="search-shipper-list"
                        ref="search-shipper-list"
                      ></el-input>
                    </div>
                    <div
                      class="list"
                      infinite-scroll-immediate
                      infinite-scroll-distance="100"
                      v-infinite-scroll="search"
                      infinite-scroll-disabled="disabled"
                    >
                      <div
                        v-bind:key="shipper.id"
                        v-for="shipper in shippers.list"
                        class="list-item"
                      >
                        <span class="span-name">{{ shipper.name }} - {{ shipper.number }}</span>
                        <span class="span-check">
                          <el-radio
                            v-model="user.shipperAccountNumber"
                            :label="shipper.number"
                            @change="change(shipper)"
                          >&nbsp;</el-radio>
                        </span>
                      </div>
                    </div>
                    <p class="loading-shippers" v-if="shippers.loading">
                      <img src="@/assets/svg/loading.svg" alt="loading" />
                    </p>
                    <p class="scroll-ellipsis" v-if="!noMore && !shippers.loading">...</p>
                  </div>
                </transition>
                <!-- <el-input
                  v-model="form.farmName"
                  v-alphanumeric-validation
                  maxlength="100"
                  clearable
                  class="inline-input"
                  id="form-farm-name"
                ></el-input>-->
              </el-form-item>
            </el-col>
          </el-col>
          <el-col :md="3" :sm="12" :xs="12" class="form-vertical-item">
            <div class="el-form-item el-form-item--mini">
              <el-tooltip
                class="item"
                effect="dark"
                :content="$t('warehouse.orderEntry.addOrder')"
                placement="top-start"
              >
                <el-button
                  id="form-save-button"
                  @click="submitForm()"
                  icon="el-icon-plus"
                  size="mini"
                  class="button-action-default margin-t-38"
                ></el-button>
              </el-tooltip>
              <el-tooltip
                class="item"
                effect="dark"
                :content="$t('warehouse.orderEntry.clear')"
                placement="top-start"
              >
                <el-button
                  id="form-refresh-button"
                  @click="resetForm()"
                  icon="fa fa-eraser"
                  size="mini"
                  class="button-action-default"
                ></el-button>
              </el-tooltip>
            </div>
          </el-col>
        </el-col>
      </el-form>
      <span slot="footer" class="dialog-footer">&nbsp;</span>
    </el-dialog>
  </span>
</template>

<script>
import { Message } from 'element-ui';
import moment from 'moment';
import { mapGetters } from 'vuex';
import constants from '@/utils/constants';
import apiConstants from '@/utils/apiConstants';
import Autocomplete from '@/components/Autocomplete';
import shipperList from '@/components/ShipperList';

const formConsignee = 'form-consignee';

export default {
  components: { Autocomplete ,shipperList},
  computed: {
    ...mapGetters(['orderEntry' ,'user', 'shippers']),
     noMore() {
      return this.shippers.list.length >= this.shippers.totalRows;
    },
    disabled() {
      return this.shippers.loading || this.noMore;
    }
  },
  data() {
    return {
      dialogVisible: false,
      searchText: '',
      form: {
        consigneeAccountId: null,
        unitOfMeasure: constants.ORDER_ENTRY.UNIT_OF_MEASURE_LIST[0].value,
        measure: constants.ORDER_ENTRY.MEASURE_LIST[0].value,
        shipDate: new Date(),
        totalUnits: null,
        length: null,
        width: null,
        height: null,
        productCode: '',
        productDescription: '',
        po: '',
        farmName: ''
      },
      formRules: {
        consigneeAccountId: [
          {
            required: true,
            message: this.$t('warehouse.orderEntry.error.consigneeRequired'),
            trigger: 'submit'
          }
        ],
        unitOfMeasure: [
          {
            required: true,
            message: this.$t(
              'warehouse.orderEntry.error.unitOfMeasureRequired'
            ),
            trigger: 'blur'
          }
        ],
        shipDate: [
          {
            required: true,
            message: this.$t('warehouse.orderEntry.error.shipDateRequired'),
            trigger: 'blur'
          }
        ],
        length: [
          {
            required: true,
            message: this.$t('warehouse.orderEntry.error.lengthRequired'),
            trigger: 'blur'
          }
        ],
        width: [
          {
            required: true,
            message: this.$t('warehouse.orderEntry.error.widthRequired'),
            trigger: 'blur'
          }
        ],
        height: [
          {
            required: true,
            message: this.$t('warehouse.orderEntry.error.heightRequired'),
            trigger: 'blur'
          }
        ],
        totalUnits: [
          {
            required: true,
            message: this.$t('warehouse.orderEntry.error.numberUnitsRequired'),
            trigger: 'blur'
          }
        ]
      },
      unitOfMeasureOptions: constants.ORDER_ENTRY.UNIT_OF_MEASURE_LIST,
      measureOptions: constants.ORDER_ENTRY.MEASURE_LIST,
      urlConsignee: apiConstants.END_POINTS.ACCOUNTS.CONSIGNEE_BY_SHIPPER,
      urlProduct: apiConstants.END_POINTS.PRODUCT_SEARCH
    };
  },
  methods: {
    handleOpen() {
      this.form.shipDate = moment(
        this.orderEntry.actualFilters.shipDate,
        'YYYY-MM-DD'
      ).toDate();
      this.$nextTick(() => {
        this.form.consigneeAccountId = this.orderEntry.actualFilters.consigneeAccountId;
        this.$refs[formConsignee].setLocalModel(
          this.orderEntry.actualFilters.consigneeName
        );
        this.$refs.form.$el[0].focus();
      });
    },
    handleClose(done) {
      this.resetForm();
      done();
    },
    async submitForm() {
      const minValue = this.orderEntry.settings.minCubesPerBox;

      let length;
      let width;
      let height;

      if (this.form.measure === constants.ORDER_ENTRY.MEASURE_LIST[1].value) {
        length = this.$refs.length.value * constants.ORDER_ENTRY.CONST_VALUE;
        width = this.$refs.width.value * constants.ORDER_ENTRY.CONST_VALUE;
        height = this.$refs.height.value * constants.ORDER_ENTRY.CONST_VALUE;
      } else {
        length = this.$refs.length.value;
        width = this.$refs.width.value;
        height = this.$refs.height.value;
      }
      const volume = length * width * height;
      if (volume <= minValue) {
        let message = this.$t('warehouse.orderEntry.error.minValueValidationError');
        message = message.replace('[unit]', this.$refs.unit.value).replace('[minValue]', minValue);
        const confirm = await this.$confirm(message, { confirmButtonText: this.$t('OK') });
        if (confirm) {
          return this.getSubmitForm();
        }
        return false;
      } if (volume > constants.ORDER_ENTRY.MAX_VALUE) {
        let message = this.$t('warehouse.orderEntry.error.maxValueValidationError');
        message = message.replace('[minValue]', minValue).replace('[max_value]', constants.ORDER_ENTRY.MAX_VALUE);
        this.$message.warning(message);
        return false;
      }
      return this.getSubmitForm();
    },
    resetForm() {
      this.$refs.form.resetFields();
      this.form.consigneeId = null;
      this.form.productCode = '';
      this.form.productDescription = '';
      this.$refs['form-product'].clearLocalModel();
      this.$refs[formConsignee].clearLocalModel();
      this.form.shipDate = moment(
        this.orderEntry.actualFilters.shipDate,
        'YYYY-MM-DD'
      ).toDate();
      this.$nextTick(() => {
        this.form.consigneeAccountId = this.orderEntry.actualFilters.consigneeAccountId;
        this.$refs[formConsignee].setLocalModel(
          this.orderEntry.actualFilters.consigneeName
        );
      });
      this.$refs.form.$el[0].focus();
    },
    /* The datepicker has an issue when change manually, the focus is lost. */
    handleChangeDate() {
      this.$refs.form.$el[4].focus();
    },
    isVisible() {
      return (
        this.$can(this.permissions.DATA_ENTRY.ADD_SHIPMENT)
        || this.$can(this.permissions.DATA_ENTRY.ADD_SHIPMENT_AFTER_CUTOFF)
      );
    },
    getSubmitForm() {
      return new Promise((resolve, reject) => {
        this.$refs.form.validate((valid) => {
          if (valid) {
            Message.closeAll();
            const loading = this.$loading(constants.LOADING.DEFAULT_CONFIG);
            this.$store
              .dispatch('orderEntry/save', this.form)
              .then((response) => {
                loading.close();
                this.$message.success(response.message);
                this.resetForm();
                this.$refs.form.$el[0].focus();
                this.$store.dispatch('orderEntry/search', {});
                resolve(response);
              })
              .catch((error) => {
                loading.close();
                reject(error);
              });
          } else {
            reject(new Error(this.$t('common.invalid')));
          }
        });
      });
    }
  },
      search(resetSearch) {
      // If sarch text is changed or reset search is mandatory we call to resetShipperList
      if ((this.searchText !== this.shippers.filters.search) || resetSearch) {
        this.$store.dispatch('account/resetShipperList');
      }
      return this.$store.dispatch('account/getShipperListScroll', this.searchText);
    },
    async change(shipperSelected) {
      this.$store.dispatch('setUserShipperAccount', shipperSelected);
      const loading = Loading.service(constants.LOADING.DEFAULT_CONFIG);
      await this.callSelectAction();
      loading.close();
      this.show = false;
    },
    openList() {
      this.show = !this.show;
      // Its necessary nextTick because we need to wait the component render.
      if (this.show) {
        this.$nextTick(() => {
          this.$refs['search-shipper-list'].focus();
        });
      }
    },
    async callSelectAction() {
      if (this.actionStrSelectChained) {
        for (const action of this.actionStrSelectChained) {
          if (action === 'orderEntry/getSettings') {
            const res = await this.$store.dispatch(action);
            this.orderEntry.settings.minCubesPerBox = res.__ob__.value.minCubesPerBox;
            // this.orderEntry.settings.minCubesPerBox = res.__ob__.value.minCubesPerBox ? res.__ob__.value : '0.9461';
          } else {
            await this.$store.dispatch(action);
          }
        }
      }
    },
  async mounted() {
    const resetSearch = true;
    if (this.user.shipperAccountNumber) {
      this.callSelectAction();
      // Call reset search but without loading because the shippers is already selected, and no need await.
      this.search(resetSearch);
    } else {
      const loading = Loading.service(constants.LOADING.DEFAULT_CONFIG);
      await this.search(resetSearch);
      this.show = this.shippers.list && this.shippers.list.length > 1;

      // SHIPPERS Users Normal Flow: If the list of shippers only returns 1, then we select it by default.
      if (this.shippers.list && this.shippers.list.length === 1) {
        this.change(this.shippers.list[0]);
      }
      loading.close();
    }

    // Its necessary nextTick because we need to wait the component render.
    if (this.show) {
      this.$nextTick(() => {
        this.$refs['search-shipper-list'].focus();
      });
    }
  }
};
</script>
